html {
    font-size: 14px;
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

a {
    text-decoration: none;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.header {
    margin: 0 0 10px 0;
    padding: 10px;
    background-color: #eee;
    border-bottom: 1px solid #aaa;
}

.header-info,
.header button {
    float: right;
}

.header-info,
.header button {
    /*margin-right: 2em;*/
}

.header-link {
    margin-left: 2em;
}

.content {
    padding: 0 10px;
}


.action-button {
    background-color: #e0e0e0;
    border: 1px solid #e0e0e0;
    border-radius: 3px;
    padding: 0 2px 1px 2px;
    text-decoration: none;
    color: #555;
    margin-left: 10px;
}

.action-button.disabled {
    background-color: #eeeeee;
    border: 1px solid #eeeeee;
    color: #aaa;
}

.action-button:hover {
    background-color: #ccccff;
    border: 1px solid #ccccff;
    text-decoration: none;
    color: blue;
}

.text-button {
    background-color: transparent;
    border: none;
    margin-right: 10px;
    padding: 0;
    text-decoration: none;
    /*color: #555;*/
    /*margin-left: 10px;*/
    font-size: 1rem;
    color: blue;
}

.text-button.active {
    font-weight: bold;
}

.text-button:focus {
    outline: none;
}


.error {
    background-color: darkorange;
    padding: 5px;
}

.dismiss {
    float: right;
}

.sync {
    float: right;
}

.infos button,
.filtering button,
.batch-actions button {
    margin: 0 5px;
}

.sorting button {
    /*margin: 0 5px;*/
}


.lists {
    width: 100%;
    display: grid;
    /*flex-direction: row;*/
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
}

.videos {
    border: 1px solid #aaa;
    padding: 5px;
}

.videos > div {
    margin: 5px 0;
}

/*.videos select {*/
    /*margin-left: 10px;*/
/*}*/

