.videos .filter,
.videos .playlist-selector {
    margin-bottom: 10px;
}
*/

.playlist-selector select {
    /*font-size: 1.25em;*/
    font-size: 1em;
    font-weight: bold;
}


.batch-actions {
    display: flex;
    flex-direction: row;
}

.row {
    display: flex;
    flex-direction: row;
    /*margin: 5px 0;*/
    padding: 2px 5px 3px 5px;
}

.row:hover {
    background-color: #fffb72;
}


.row-0 {
    /*background-color: #E5E5FB;*/
}

.row-1 {
    background-color: #ffffff;
}

.marked {
    background-color: #fffb72;
}

.video-title {
    flex-grow: 2;
    margin-left: 1em;
    margin-right: 1em;
}


.subscriptions-list {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 10px 10px;
}

.subscriptions-list a {
    text-decoration: none;
    margin-right: 10px;
}

.channel-playlists {
    font-size: 0.85em;
}

